.comment{
  margin-top: 1.5rem;
  display: flex;
  gap: 1rem; 
}

.commentBox{
  flex: 1;
}

.commentContent{
  background: #f9f9f9;
  border-radius: 8px;
  padding: 1rem; 
}

.commentContent header{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;  
}

.authorAndTime strong{
  display: block;
  margin-right: 30rem;
  font-size: 0.875rem;
  line-height: 1.6;
}

.authorAndTime time{
  display: block;
  font-size: 0.75rem;
  line-height: 1.6;
  color: var(--gray-400);
}

.commentContent header button{
  background:transparent;
  border: 0;
  color: var(--gray-400);
  cursor: pointer;
  line-height: 0;
  border-radius: 2px;
}

.commentContent header button:hover{
  color: var(--red-500);
}

.commentContent p{
  margin-top: 1rem;
  color: var(--gray-300);
}

.commentBox footer{
  margin-top: 1rem;
  display: grid;
  justify-content: flex-start;
}

.commentBox footer button{
  background: transparent;
  border: 0;
  color: var(--gray-400);
  cursor: pointer;

  display: flex;
  align-items: center;

  border-radius: 2px;
}

.commentBox footer button:hover{
  color: var(--green-300);
}

.commentBox footer button svg{
  margin-right: 0.5rem;
}

.commentBox footer button span::before{
  content: "\2022";
  padding: 0 0.25rem;
}

@media (max-width: 768px){
  .comment{
    width: 100%;    
    display:flex;
    align-items: center;
    margin-left: 0;
    box-sizing: border-box;
  }  

  .commentContent{
    max-width: 49%;
    width: 100%; 
    box-sizing: border-box;
    margin-left: 0;
  }

  .commentContent header button{
    position: absolute;
    right: 5rem;
  }
}